}

.color-palette-horizontal .main-color {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex
}

.color-palette-horizontal .main-color-item {
  position: relative;
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  height: 86px;
  margin-right: 0;
  padding: 37px 0 0;
  line-height: normal;
  text-align: center;
  border-radius: 0
}

.color-palette-horizontal .main-color-item .main-color-text {
  float: none
}

.color-palette-horizontal .main-color-item:hover {
  height: 96px;
  margin-top: -10px;
  border-radius: 4px 4px 0 0
}

.color-palette-horizontal .main-color-value {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  text-align: center;
  -webkit-transform-origin: unset;
  -ms-transform-origin: unset;
  transform-origin: unset
}

.color-palette-horizontal .main-color:hover .main-color-item {
  padding-top: 8px
}

.color-palette-horizontal .main-color:hover .main-color-value {
  bottom: 8px;
  opacity: .7
}

ul.xgicons-list {
  margin: 10px 0;
  overflow: hidden;
  list-style: none
}

ul.xgicons-list li {
  position: relative;
  float: left;
  width: 16.66%;
  height: 100px;
  margin: 3px 0;
  padding: 10px 0 0;
  overflow: hidden;
  color: #555;
  text-align: center;
  list-style: none;
  background-color: #fff;
  border-radius: 4px;
  cursor: pointer;
  -webkit-transition: color .3s ease-in-out,background-color .3s ease-in-out;
  transition: color .3s ease-in-out,background-color .3s ease-in-out
}

ul.xgicons-list li .xgicon {
  margin: 12px 0 8px;
  font-size: 36px;
  -webkit-transition: -webkit-transform .3s ease-in-out;
  transition: -webkit-transform .3s ease-in-out;
  transition: transform .3s ease-in-out;
  transition: transform .3s ease-in-out, -webkit-transform .3s ease-in-out;
  will-change: transform
}

ul.xgicons-list li .xgicon-class {
  display: block;
  font-family: 'Lucida Console',Consolas,Monaco,'Andale Mono','Ubuntu Mono',monospace;
  white-space: nowrap;
  text-align: center;
  -webkit-transform: scale(.83);
  -ms-transform: scale(.83);
  transform: scale(.83)
}

ul.xgicons-list li .xgicon-class .xg-badge {
  -webkit-transition: color .3s ease-in-out;
  transition: color .3s ease-in-out
}

ul.xgicons-list li:hover {
  color: #fff;
  background-color: #1890ff
}

ul.xgicons-list li:hover .xgicon {
  -webkit-transform: scale(1.4);
  -ms-transform: scale(1.4);
  transform: scale(1.4)
}

ul.xgicons-list li:hover .xg-badge {
  color: #fff
}

ul.xgicons-list li.outlined:hover {
  background-color: #8ecafe
}

ul.xgicons-list li.copied:hover {
  color: rgba(255,255,255,0.2)
}

ul.xgicons-list li::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: #fff;
  line-height: 110px;
  text-align: center;
  opacity: 0;
  -webkit-transition: all .3s cubic-bezier(.18, .89, .32, 1.28);
  transition: all .3s cubic-bezier(.18, .89, .32, 1.28);
  content: 'Copied!'
}

ul.xgicons-list li.copied::after {
  top: -10px;
  opacity: 1
}

