.santa {
  display: inline-block;
  margin-right: 8px;
  margin-left: 8px;
  vertical-align: middle
}

.santa-body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  width: 1em;
  height: 1em;
  color: #f91047;
  font-size: 24px;
  background-color: currentColor;
  border-radius: 50%;
  -webkit-box-shadow: inset 0 -0.25em rgba(0,0,0,0.1);
  box-shadow: inset 0 -0.25em rgba(0,0,0,0.1);
  -webkit-transform-origin: center bottom;
  -ms-transform-origin: center bottom;
  transform-origin: center bottom;
  -webkit-animation: balance alternate infinite 2s ease-in-out;
  animation: balance alternate infinite 2s ease-in-out
}

.santa-head {
  position: relative;
  width: 1em;
  height: 1.9em;
  font-size: .4em;
  background-color: white;
  border-radius: .5em;
  -webkit-transform: translateY(-1em);
  -ms-transform: translateY(-1em);
  transform: translateY(-1em)
}

.santa-head::before {
  position: absolute;
  top: .65em;
  left: 0;
  display: block;
  width: 1em;
  height: .375em;
  background-color: #ff9876;
  content: ''
}

.santa-ear {
  position: absolute;
  top: .75em;
  width: .1em;
  height: .3em;
  background-color: #fc8363
}

.santa-ear:nth-of-type(1) {
  left: -0.1em;
  border-radius: .05em 0 0 .05em
}

.santa-ear:nth-of-type(2) {
  right: -0.1em;
  border-radius: 0 .05em .05em 0
}

.santa-hat {
  position: absolute;
  top: .5em;
  left: 0;
  width: 1em;
  height: .15em;
  background-color: white;
  -webkit-transform: scale(1.1);
  -ms-transform: scale(1.1);
  transform: scale(1.1);
  content: ''
}

.santa-hat::before {
  position: absolute;
  top: -0.5em;
  z-index: 2;
  display: block;
  width: 1em;
  height: .5em;
  background: #f91047;
  border-radius: .5em .5em 0 0;
  content: ''
}

.santa-hat::after {
  position: absolute;
  top: -0.72em;
  right: 0;
  z-index: 0;
  display: block;
  width: .25em;
  height: .25em;
  background-color: white;
  border-radius: 50%;
  -webkit-box-shadow: -0.2em .2em 0 .12em rgba(0,0,0,0.2),-0.2em .2em 0 .12em #f91047;
  box-shadow: -0.2em .2em 0 .12em rgba(0,0,0,0.2),-0.2em .2em 0 .12em #f91047;
  content: ''
}

.santa-eye {
  position: absolute;
  top: .76em;
  left: .2em;
  width: .12em;
  height: .12em;
  background-color: black;
  border-radius: 50%
}

.santa-eye+.santa-eye {
  right: .2em;
  left: auto
}

.santa-nose {
  position: absolute;
  top: .84em;
  left: 50%;
  width: .12em;
  height: .22em;
  background-color: #f24c4c;
  border-radius: 0 0 .12em .12em;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%)
}

.santa-mouth {
  position: absolute;
  top: 50%;
  left: 50%;
  width: .18em;
  height: .1em;
  margin-top: .3em;
  background-color: black;
  border-bottom-right-radius: 5vw;
  border-bottom-left-radius: 5vw;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  -webkit-animation: hohoho 4s linear forwards infinite;
  animation: hohoho 4s linear forwards infinite
}

@-webkit-keyframes hohoho {
  0%,10%,20%,40%,100% {
    width: .18em;
    height: .1em;
    border-bottom-right-radius: 1vw;
    border-bottom-left-radius: 1vw
  }

  5%,15%,25%,35% {
    width: .15em;
    height: .2em;
    border-radius: 50%
  }
}

@keyframes hohoho {
  0%,10%,20%,40%,100% {
    width: .18em;
    height: .1em;
    border-bottom-right-radius: 1vw;
    border-bottom-left-radius: 1vw
  }

  5%,15%,25%,35% {
    width: .15em;
    height: .2em;
    border-radius: 50%
  }
}

@-webkit-keyframes balance {
  from {
    -webkit-transform: rotate(-4deg);
    transform: rotate(-4deg)
  }

  to {
    -webkit-transform: rotate(4deg);
    transform: rotate(4deg)
  }
}

@keyframes balance {
  from {
    -webkit-transform: rotate(-4deg);
    transform: rotate(-4deg)
  }

  to {
    -webkit-transform: rotate(4deg);
    transform: rotate(4deg)
  }
}
