@import "../fonts/iconfont.css";
/*字体大小*/
.fSize12 {
  font-size: 12px;
  line-height: 20px;
}
.fSize14 {
  font-size: 14px;
  line-height: 22px;
}
.fSize16 {
  font-size: 16px;
  line-height: 24px;
}
.fSize20 {
  font-size: 20px;
  line-height: 28px;
}
.fSize24 {
  font-size: 24px;
  line-height: 32px;
}
.fSize30 {
  font-size: 30px;
  line-height: 38px;
}
.fSize32 {
  font-size: 32px;
  line-height: 40px;
}
.fSize38 {
  font-size: 38px;
  line-height: 46px;
}
.fSize46 {
  font-size: 46px;
  line-height: 54px;
}
.fSize56 {
  font-size: 56px;
  line-height: 64px;
}
.fSize68 {
  font-size: 68px;
  line-height: 76px;
}
/*字体加粗*/
.fBold {
  font-weight: bold;
}
/*字体颜色*/
.titleColor {
  /*Title 标题 字体颜色*/
  color: rgba(0, 0, 0, 0.85);
}
.bodyColor {
  color: rgba(0, 0, 0, 0.65);
}
.captionColor {
  color: rgba(0, 0, 0, 0.45);
}
.disableColor {
  color: rgba(0, 0, 0, 0.25);
}
.linkColor {
  color: #1890FF;
}
/*图标*/
.icon {
  width: 1em;
  height: 1em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}
.dropdownGroup {
  position: relative;
  display: inline-flex;
}
.dropdownGroup .xg-dropdown {
  top: 0px;
  left: 0;
}
.dropdownGroup_lg .xg-dropdown {
  top: 0px;
  left: 0;
}
.dropdownGroup_sm .xg-dropdown {
  top: 0px;
  left: 0;
}
.fade-enter,
.fade-appear {
  opacity: 0;
}
.fade-enter-active,
.fade-appear-active {
  opacity: 1;
  transition: opacity 1s ease-in;
}
.fade-enter-done {
  opacity: 1;
}
.fade-exit {
  opacity: 1;
}
.fade-exit-active {
  opacity: 0;
  transition: opacity 1s ease-in;
}
.fade-exit-done,
.op {
  opacity: 0;
}
.xg-menu-overflow-hidden {
  overflow: hidden;
}
